.NotFound {
    text-align: center;
  --color-text:#fff;
  --color-bg:#143261;
  --color-bg-2:#010915;
  --color-link:#e0ded4;
  --color-link-hover:#e2c391;
  background:radial-gradient(ellipse at 50% 50%,var(--color-bg) 40%,var(--color-bg-2) 150%)
}

  
.NotFound-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
.NotFound-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    position: relative;
  }