
.aws-btn {
    --button-default-height: 83px;
    --button-default-font-size: 25px;
    --button-default-border-radius: 13px;
    --button-horizontal-padding: 36px;
    --button-raise-level: 8px;
    --button-hover-pressure: 2.5;
    --transform-speed: 0.225s;
    --button-primary-color: #475472;
    --button-primary-color-dark: #2a3143;
    --button-primary-color-light: #d4d9e4;
    --button-primary-color-hover: #424e6a;
    --button-primary-color-press:  #424e6a;
    --button-primary-border: none;

}

